@font-face {
    font-family: 'MyriadPro-Bold';
    src: url(../assets/Font/Myriad_Pro/MyriadPro-Bold.otf);
}
/* @font-face {
    font-family: 'MyriadPro-Bold';
    src: url(../assets/Font/Myriad_Pro/MyriadPro-Bold.otf);
} */
.home{
    display: flex;
    flex-direction: column;
    /* height: 880px;
    width:460px; */
    width: 100%;
    height: 98.2vh;
    background-image: linear-gradient(180deg, #000000,#1b0a02,#2c0f02,#3e1503, #642002,#642002,#642002,#642002,#642002);
    position: relative;
    min-width: 360px;
}

/* .home div{
    border-style: solid;
} */
.topbar{
    display: flex;
    justify-content: space-between;
    height: 3%;
    margin: 30px 20px 0px 20px;
}
.topbar-cancel{
    display: flex;
    align-items: center;
    color: white;
}
.topbar-title{
    display: flex;
    flex-direction: column;
    color: white;
    align-items: center;
    justify-content: center;
}
.topbar-title-amarna{
    font-size: larger;
    font-family: Arial, Helvetica, sans-serif;

}
.topbar-detail{
    display: flex;
    align-items: center;
}
.info{
    display: flex;
    justify-content: space-around;
    height: 15%;
    margin: 0px 20px 0px 20px;
}
.info-avatar{
    display: flex;
    align-items: center;
    height: 100%;
    width:50%;
}
.info-avatar-imgbox{
    width:50px;
    height:50px;
    background-color: white;
    border-radius: 5px;
}
.info-avatar-text{
    color:white;
    margin-left: 10px;
}
.info-quest{
    width:50%;
    margin-left:20px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.info-quest-img{
    width: 40px;
    height: 40px;
    position: absolute;
    left: -25px;
}
.info-quest-text{
    border-radius: 5px;
    display: flex;
    align-items: center;
    height: 20%;
    font-size: 10px;
    margin-left:20px;
    padding-left: 10px;
    padding-right : 5px;
    background-color: #464646;
    color: white;
}
.mask{
    width: 100%;
    height: 90%;
    /* margin-top: 20%; */
    background-color: black;
    border-radius: 2rem 2rem 0 0;
    border-top-color: #b93224;
    border-top-style: solid;
    /* background: linear-gradient(to bottom, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.1)); */
    /* , url('your-image.jpg'); */
    /* background-size: cover;
    background-blend-mode: overlay; */
    background: linear-gradient(to bottom, rgba(18, 18, 18, 1),rgba(18, 18, 18, 1),rgba(18, 18, 18, 1), rgba(255, 255, 255, 0));
    display: flex;
    flex-direction: column;

    min-width: 360px;
}
.mask > div,img{
    z-index: 1;
}
.hide{
    display: flex;
    margin-top:20px;
    justify-content: center;
    align-items: center;
    height : 30%;
    z-index: 1;
}
.hide-img:active{
    opacity: 0.8;
    height: 70%;
    width: 60%;
}
.hide-img{
    height: 220px;
    width: 320px;
    opacity: 1;
}
.background{
    width: 100%;
    height: 60%;
    position: absolute;
    background-image: linear-gradient(to bottom, rgba(18, 18, 18, 1), rgba(18, 18, 18, 0.4),rgba(18, 18, 18, 0.3),rgba(18, 18, 18, 0.2),rgba(18, 18, 18, 0),rgb(54, 51, 51,0), rgba(255, 255, 255, 0)),url(../assets/images/Homepage/background.png);
    background-repeat: no-repeat;
    background-size: contain,100% 100%;
    bottom: 0;
    z-index: 0;
    
}
.vase{
        display: flex;
        width: auto;
        justify-content: center;
        align-items: flex-end;
        height: 80%;
        z-index: 1;
}
.vase-coin{
    height: 80%;
}
.vase-img-big{
    /* position: absolute; */
    width:95px;
    height:170px;
    /* bottom: 75px; */
    background-image: url(../assets/images/Homepage/vase.png);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}
.vase-img-small{
    /* position:absolute; */
    margin-bottom:10px;
    width:85px;
    height:150px;
    /* bottom: 75px; */
    background-image: url(../assets/images/Homepage/vase.png);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}
.panel{
    display:flex;
    justify-content : space-between;
    align-items: center;
    height : 15%;
    margin: 0px 10px 0px 10px;
}
.panel-score{
    display: flex;
    width: 50%;
    justify-content: center;
}
.panel-score-img{
    width:45px;
    height:50px;
}
.panel-score-text{
    height:50px;
    margin-left: 20px;
    font-size: 38px;
    font-weight:bolder;
    font-family: 'MyriadPro-Bold';
    background: -webkit-linear-gradient(#fff3bf,#fff3bf,#fff3bf,#fff3bf,#ffd93a,#ffd93a,#ffd93a,#ffd93a);
    /* background: -webkit-linear-gradient(#fff3bf,#ffd93a); */
    background-clip:padding-box;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    /* color: #ffd93a; */
}

.temp-div {
    background-image: url('../assets/images/Homepage/btns.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    width:80px;
    height:60px;
    font-weight: bold;
    font-family: 'MyriadPro-Bold';
}
/* .coin:start
*/

.coin{
    height: 10%;
    position: absolute;
    bottom:500px;
    left:48%;
    right: 48%;
    z-index: 0;
    opacity: 0;
}
#coin {
    background-image: url(../assets/images/Homepage/amarIcon.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    width: 20px;
    height: 30px;
}
.human{
    position: absolute;
    left: 0;
    width: 200px;
    height: 400px;
}
.dog{
    position: absolute;
    right: 0;
    width: 180px;
    height: 350px;
}



@keyframes coin_down {
    from {
        bottom: 400px;
        opacity: 1;
        /* transform: rotateX(0); */
    }
    to {
        opacity: 0;
        bottom: 180px;
        /* transform: rotateX(360deg); */
    }
}


#cup_1 {
    animation: cup_animation_1 0.4s infinite forwards;
  }

  @keyframes cup_animation_1 {
    0% {
    }

    33% {
        filter: blur(5px);
        transform: translate3d(90px, 5px, 10px) scale(1.1);
    }

    66% {
        filter: blur(0px);
        transform: translate3d(180px, 0px, 0px);
    }

    100% {
    }
  }
  #cup_2 {
    animation: cup_animation_2 0.4s infinite forwards;
  }

  @keyframes cup_animation_2 {
    0% {
    }

    33% {
        filter: blur(5px);
        transform: translate3d(90px, 0px, 10px) scale(0.9);
    }

    66% {
        filter: blur(0px);
        transform: translate3d(-90px, 0px, 0px) scale(0.9);
    }

    100% {
    }
  }
  #cup_3 {
    animation: cup_animation_3 0.4s infinite forwards;
  }

  @keyframes cup_animation_3 {
    0% {
    }

    33% {
        filter: blur(5px);
        transform: translate3d(-180px, 0px, 0px);
    }

    66% {
        filter: blur(0px);
        transform: translate3d(-90px, 5px, 5px) scale(1.1);
    }

    100% {
    }
  }
  @media screen and (max-width: 519px) {
    .panel-score-img{
        width:20px;
        height:25px;
    }
    .panel-score-text{
        height:25px;
        margin-left: 10px;
        font-size: 25px;
        font-weight:bolder;
        font-family: 'MyriadPro-Bold';
        background: -webkit-linear-gradient(#fff3bf,#fff3bf,#fff3bf,#fff3bf,#ffd93a,#ffd93a,#ffd93a,#ffd93a);
        /* background: -webkit-linear-gradient(#fff3bf,#ffd93a); */
        background-clip:padding-box;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        /* color: #ffd93a; */
    }
    .panel{
        display:flex;
        justify-content : space-between;
        align-items: center;
        height : 15%;
        margin: 0px 20px 0px 20px;
    }
  }
